<template>
  <div class="header">
    <div class="header-left">
     <i class="iconfont icon-fanhui"></i>
    </div>
    <div class="header-input">
     <i class="iconfont icon-sousuo"></i>
      输入城市/景点/游玩主题
    </div>
    <router-link to='/city'>
      <div class="header-right">
        {{city}}
      <i class="iconfont icon-xiala"></i>
      </div>
    </router-link>
  </div>
</template>
<script>
export default {
  name: 'home',
  props:['city']
}
</script>
<style lang='stylus' scoped>
  @import '~styles/varibles.styl'
 .header
    display: flex
    line-height: $headerHeight
    background: $bgColor
    color: #fff
    .header-left
      text-align:center
      width: .64rem
      float: left
      .back-icon
        text-align: center
        font-size: .4rem
    .header-input
      flex: 1
      height: .64rem
      line-height: .64rem
      margin-top: .12rem
      margin-left: .2rem
      padding-left: .2rem
      background: #fff
      border-radius: .1rem
      color: #ccc
    .header-right
      min-width: 1.04rem
      padding: 0 .1rem
      float: right
      text-align: center
      color: #fff
      .arrow-icon
        margin-left: -.04rem
        font-size: .24rem

</style>


