<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide >



         <div
          class="icon"

        >
        <router-link to='/local'>
          <div class='icon-img'>
           <i class=' iconfont icon-lvyou1'></i>
          </div>
          <p class="icon-desc">当地旅游</p>
          </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/hotel'>
          <div class='icon-img'>
           <i class=' iconfont icon-jiudian'></i>
          </div>
          <p class="icon-desc">精选酒店</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/carrent'>
          <div class='icon-img'>
           <i class=' iconfont icon-chuzu'></i>
          </div>
          <p class="icon-desc">汽车出租</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/xingli'>
          <div class='icon-img'>
           <i class=' iconfont icon-weibiaoti-'></i>
          </div>
          <p class="icon-desc">行李寄存</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/ticket'>
          <div class='icon-img'>
           <i class=' iconfont icon-mg-ticket'></i>
          </div>
          <p class="icon-desc">景点门票</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/air'>
          <div class='icon-img'>
           <i class=' iconfont icon-lvyou'></i>
          </div>
          <p class="icon-desc">优惠机票</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/hanking'>
          <div class='icon-img'>
           <i class=' iconfont icon-icon-test'></i>
          </div>
          <p class="icon-desc">驻地探险</p>
        </router-link>
        </div>
         <div
          class="icon"

        >
        <router-link to='/food'>
          <div class='icon-img'>
           <i class=' iconfont icon-meishi'></i>
          </div>
          <p class="icon-desc">特色美食</p>
        </router-link>
        </div>



      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    margin-top: .1rem

    .icon
      position: relative
      overflow: hidden
      float: left
      width: 25%
      height: 0
      padding-bottom: 25%
      .icon-img
        position: absolute

        top: 0
        left: 0
        right: 0
        bottom: .44rem
        box-sizing: border-box
        text-align: center
        padding: .1rem
        .iconfont
          font-size :50px
          color:$bgColor
          position:relative
          top:9px



        .icon-img-content
          display: block
          margin: 0 auto
          height: 100%
      .icon-desc
        position: absolute

        left: 0
        right: 0
        bottom: 0
        height: .44rem
        line-height: .44rem
        text-align: center
        color: $darkTextColor
        ellipsis()
</style>




